    display: none;
}

.task-form {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: 10px;
    padding: 20px;
    min-width: 500px;
    max-width: fit-content;
}

.task-form fieldset {
    grid-column: span 2;
}

.task-form fieldset legend{
    padding: 0px 5px;
}

.task-form fieldset input[type="radio"] {
    margin-right: 30px;
}

.task-form .task-submit {
    padding: 10px 20px;
    grid-column: span 2;
}

.task-create-error {
    grid-column: span 2;
    display: none;
    background-color: lightcoral;
    width: 100%;
    padding: 10px;
}

.task-form input[readonly]{
    background-color: lightgray;
    outline: none;
    border: none;
}`, "",{"version":3,"sources":["webpack://./src/taskForm.css"],"names":[],"mappings":"AAAA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,+BAA+B;IAC/B,SAAS;IACT,aAAa;IACb,gBAAgB;IAChB,sBAAsB;AAC1B;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,4BAA4B;IAC5B,WAAW;IACX,aAAa;AACjB;;AAEA;IACI,2BAA2B;IAC3B,aAAa;IACb,YAAY;AAChB","sourcesContent":[".task-form-container {\n    display: none;\n}\n\n.task-form {\n    display: grid;\n    grid-template-columns: auto 1fr;\n    gap: 10px;\n    padding: 20px;\n    min-width: 500px;\n    max-width: fit-content;\n}\n\n.task-form fieldset {\n    grid-column: span 2;\n}\n\n.task-form fieldset legend{\n    padding: 0px 5px;\n}\n\n.task-form fieldset input[type=\"radio\"] {\n    margin-right: 30px;\n}\n\n.task-form .task-submit {\n    padding: 10px 20px;\n    grid-column: span 2;\n}\n\n.task-create-error {\n    grid-column: span 2;\n    display: none;\n    background-color: lightcoral;\n    width: 100%;\n    padding: 10px;\n}\n\n.task-form input[readonly]{\n    background-color: lightgray;\n    outline: none;\n    border: none;\n}"],"sourceRoot":""}]);
